@font-face {
    font-family: 'Pokemon Solid';
    src: url('/Pokemon Solid.ttf') format('truetype');
}

.App {
    text-align: center;
    font-family: 'Arial', sans-serif;
    width: 720px;
    height: 1280px;
    border: 2px solid black;
    overflow: hidden;
}

.container {
    overflow: hidden;
}

body {
    background: linear-gradient(45deg, #FF2CAA 0%, #ffff01 100%);
    overflow: hidden;
}

p {
    font-family: "Pokemon Solid", sans;
    color: white;
    text-shadow: 2px 2px 2px rgb(32, 32, 32);
    font-size: 20px;
}

p.answer {
    font-family: "Pokemon Solid", sans;
    letter-spacing: 3px;
}

p.answername {
    font-size: 50px;
    width: 300px;
    height: 150px;
    letter-spacing: 1px;
    transform: translateY(-20px);
}

h1 {
    font-family: "Pokemon Solid", sans;
    margin: 0 auto;
    color: white;
    text-shadow: 5px 5px 5px rgb(32, 32, 32);
    letter-spacing: 5px;
    font-size: 60px;
    float: left;
}

h1.WhosThat {
    margin-top: -60px;
    margin-bottom: -90px;
    margin-left: 20px;
}

h1.Pokemon {
    margin-left: 40px;
}

.img-wrapper {
    position: absolute;
    padding-top: 20px;
    padding-bottom: 20px;
    transform: translateX(-10px) translateY(130px);
    width: 450px;
    height: 450px;
    margin: 0 auto;
    filter: drop-shadow(6px 6px 6px rgb(32, 32, 32));
    z-index: 1000;
}

img {
    width: 100%;
    height: 100%;
    display: block;
}

.overlay {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgb(42, 85, 177);
    mix-blend-mode: multiply;
    pointer-events: none;
}

input {
    padding: 10px;
    margin-top: 20px;
    margin-right: 5px;
}

button {
    padding: 10px 20px;
    margin: 5px;
    cursor: pointer;
}

.answer {
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

.timerWrapper {
    display: flex;
    justify-content: center;
    position: relative;
    z-index: 0;
    transform: translateX(-5px) translateY(30px);
    font-size: 40px;
    color: white;
    text-shadow: 2px 2px 2px rgb(32, 32, 32);
}

.answerWrapper {
    width: 300px;
    height: 150px;
    display: flex;
    position: absolute;
    transform: translateX(405px) translateY(-30px);
    text-align: center;
    align-items: center;
    justify-content: center;
    font-size: auto;
}

.SubmissionThanks {
    width: 300px;
    height: 100px;
    display: flex;
    position: absolute;
    transform: translateY(170px);
    font-size: 25px;
    animation: color-change 5s infinite;
    line-height: 60px;
}

@keyframes color-change {
  0% { color: rgb(255, 88, 88); }
  40% { color: rgb(81, 255, 0); }
  60% { color: rgb(0, 225, 255); }
  80% { color: rgb(150, 105, 255); }
  100% { color: rgb(248, 51, 255); }
}

.confettiImage {
    width: 50px;
    height: 50px;
    transform: translateX(145px) translateY(-20px);
}

.GiftWrapper {
    width: 330px;
    height: 100px;
    transform: translateY(200px) translateX(400px);
}

.ConfettiInstructions {
    margin-top: -20px;
    line-height: 40px;
}

.leaderboard-wrapper {
    width: 700px;
    height: 400px;
    overflow-y: hidden;
    transform: translateY(380px) translateX(5px);
}
  
.leaderboard {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    height: 80%;
    color: white;
    text-shadow: 2px 2px 2px rgb(32, 32, 32);
}
  
.player {
    width: 25%;
    font-size: 20px;
}